.g-app {
  position: relative;
  height: 100vh; }
  .g-app .g-header {
    height: 50px;
    background: #314659; }
    .g-app .g-header h3 {
      color: #fff;
      font-size: 2em;
      width: 230px;
      text-align: center;
      line-height: 50px; }
  .g-app .g-body {
    position: relative;
    height: calc(100vh - 50px);
    overflow: hidden; }
  .g-app .g-side {
    height: 100%;
    width: 230px;
    float: left;
    background: #314659;
    overflow: hidden;
    height: calc(100vh - 50px); }
    .g-app .g-side .ant-menu {
      height: 100%; }
  .g-app .g-main {
    height: 100%;
    background: #fff;
    overflow: auto; }
  .g-app .g-container {
    min-width: 1120px;
    padding: 15px; }

